/***
Customized Bootstrap Labels
***/

.label {
	text-shadow: none !important;
	font-size: 14px;
	font-weight: 300;
	padding: 3px 6px 3px 6px;
	color: #fff;
	font-family: $font-family-primary;

	&.label-sm {
		font-size: 13px;
		padding: 2px 5px 2px 5px;
	}

	h1 &,
	h2 &,
	h3 &,
	h4 &,
	h5 &,
	h6 & {
		font-size: 75%;
	}
}

/* Labels variants */

.label-default {
	@include label-variant($brand-default);
}

.label-primary {
	@include label-variant($brand-primary);
}

.label-success {
	@include label-variant($brand-success);
}

.label-info {
	@include label-variant($brand-info);
}

.label-warning {
	@include label-variant($brand-warning);
}

.label-danger {
	@include label-variant($brand-danger);
}

/***
Iconic labels
***/

.label.label-icon {
	padding: 4px 0px 4px 4px;
	margin-right: 2px;
	text-align: center !important;

	> i {
		font-size: 12px;
		text-align: center !important;
	}

	.ie8 &,
	.ie9 & {
		padding: 3px 0px 3px 3px;
	}
}

/***
Text states
***/

.text-default {
	color: $brand-default;
}

.text-primary {
	color: $brand-primary;
}

.text-success {
	color: $brand-success;
}

.text-info {
	color: $brand-info;
}

.text-warning {
	color: $brand-warning;
}

.text-danger {
	color: $brand-danger;
}

@if $theme-type == "material-design" {
	.label:not(.md-skip) {
		@extend .md-shadow-z-1;
		text-transform: uppercase;
		padding: 2px 6px 4px 6px;
		font-size: 10px;
		font-weight: 600;

		&.label-sm {
			font-size: 10px;
			font-weight: 600;
			padding: 3px 6px 3px 6px;
		}
	}

	.label:not(.md-skip).label-icon {
		padding: 4px 0px 4px 4px !important;
		font-size: 12px !important;

		.ie8 &,
		.ie9 & {
			padding: 3px 0 3px 3px !important;
		}
	}
}