/***
Color library demo
***/
.color-demo {
  border: 1px solid #eee;
  margin: 0 0 20px 0;
  cursor: pointer;
}

.color-demo .color-view {
  padding: 35px 10px;
  text-align: center;
  font-size: 18px;
}

.color-demo .color-info {
  border-top: 1px solid #eee;
  padding: 10px 10px;
  text-align: center;
}

.mt-content-row {
  display: table;
  height: 100%;
  width: 100%;
}

.mt-content-row .mt-content-col-fluid {
  display: table-cell;
}

.mt-content-row .mt-content-col-fluid .mt-content-body {
  margin: 0;
  height: 100%;
  width: 100%;
  display: table;
}

.mt-content-row .mt-content-col-fixed {
  display: table-cell;
}

.mt-content-row .mt-content-col-fixed .mt-content-sidebar {
  height: 100%;
  width: 100%;
  display: table;
  margin-left: 20px;
  border: 1px solid red;
}

@media (max-width: 767px) {
  /* 767px */
  .mt-content-row {
    display: block;
    width: 100%;
  }
  .mt-content-row .mt-content-col-fluid {
    display: block;
    width: 100%;
    margin-bottom: 20px;
  }
  .mt-content-row .mt-content-col-fluid .mt-content-body {
    margin: 0;
  }
  .mt-content-row .mt-content-col-fixed {
    display: block;
    width: 100%;
  }
  .mt-content-row .mt-content-col-fixed .mt-content-sidebar {
    margin-left: 0;
  }
}
